@font-face {
  font-family: "Montserrat-Bold";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../Montserrat-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat-Regular";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Merriweather-Bold";
  src: url("../fonts/Merriweather-Bold.woff2") format("woff2"), url("../Merriweather-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../Roboto-Light.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

header {
  background-color: #eff0f2;
  height: 806px;
}

.header__logo {
  border: 6px #170f1c solid;
  height: 46px;
  width: 113px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  font-family: Montserrat-Bold;
  margin-bottom: 194px;
}

.header__logo-img {
  position: relative;
  bottom: -3px;
}

.header__inner {
  padding-top: 64px;
}

.header__title {
  text-transform: uppercase;
  font-size: 1.5em;
  font-weight: 800;
  text-align: center;
  color: #505050;
  text-shadow: 0px 9px 10px rgba(0, 0, 0, 0.24);
  font-family: Montserrat-Bold;
  margin-bottom: 153px;
}

.header__title-bg {
  background-color: #b2f2ee;
  padding: 0 11px;
}

.header__title-bg:first-child {
  padding-left: 3px;
}

.header__button {
  text-align: center;
}

.header__button-link {
  font-size: 17px;
  color: #3c3c3d;
  font-family: Montserrat;
  font-weight: 600;
  text-transform: uppercase;
  padding: 14px 46px;
  border: 2px solid #777879;
  letter-spacing: 2.5px;
  word-spacing: 3px;
  transition: background-color .2s linear, color .2s linear;
}

.header__button-link:hover {
  background-color: #3c3c3d;
  color: #fff;
}

.about {
  background-color: #f8fafa;
  height: 985px;
}

.about__description {
  display: grid;
  grid-template-columns: 393px 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 152px;
  grid-row-gap: 0px;
  padding-left: 53px;
}

.about__text {
  max-width: 495px;
  padding: 25px 0 0 25px;
  letter-spacing: 1.8px;
}

.about__title {
  font-size: 20px;
  font-family: Merriweather;
  font-weight: 700;
  padding-bottom: 22px;
  text-transform: uppercase;
  color: #3e3f3f;
}

.about__lead {
  font-size: 16px;
  font-family: Raleway;
  color: #404141;
  line-height: 1.6;
}

.about__last {
  padding-top: 22px;
  padding-bottom: 27px;
}

.about__title-sm {
  font-size: 17px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.block-title {
  text-align: center;
  padding: 118px;
}

.block-title-last {
  text-align: center;
  padding: 100px;
}

.title {
  color: #181919;
  font-size: 39px;
  font-weight: 700;
  font-family: Montserrat;
  padding-bottom: 2px;
  text-transform: uppercase;
  position: relative;
  right: -11px;
  word-spacing: 5px;
}

.subtitle {
  color: #858686;
  font-size: 18px;
  font-family: Merriweather;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.works {
  height: 1160px;
}

.works__img {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 13px 14px;
  grid-template-areas: ". . ." ". . .";
}

.works__img-item {
  height: auto;
  width: 100%;
}

.team {
  padding-bottom: 200px;
  background-color: #f9fbfb;
}

.team__img {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 13px 15px;
  grid-template-areas: ". . ." ". . .";
}

.team__img-block {
  position: relative;
}

.team__img-block:hover .team__popup {
  opacity: 1;
  pointer-events: all;
  bottom: 15px;
}

.team__img-item {
  height: auto;
  width: 100%;
}

.team__popup {
  background-color: #70f6eb;
  max-width: 335px;
  width: 100%;
  padding: 37px 37px 35px 35px;
  box-shadow: 0px 7px 16px 0px rgba(20, 20, 21, 0.18);
  position: absolute;
  left: 50%;
  bottom: 25px;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity .2s linear, bottom .2s linear;
  pointer-events: none;
}

.team__popup-name {
  text-transform: uppercase;
  color: #2b3131;
  font-family: Merriweather;
  font-weight: 700;
  font-size: 19px;
  margin-bottom: 18px;
}

.team__popup-profession {
  color: #757f7e;
  font-style: italic;
  font-size: 14px;
  font-weight: 200;
  text-transform: none;
}

.team__popup-description {
  font-size: 16px;
  font-family: Raleway;
  font-weight: 400;
  color: #2b3131;
  line-height: 1.5;
  padding-bottom: 40px;
}

.team__popop-icons {
  display: flex;
}

.team__popup-icon:not(:last-child) {
  margin-right: 16px;
}

.team__shape {
  position: absolute;
  left: 5px;
  top: -20px;
}

.contact {
  background: url("../img/background.jpg") center/cover;
  position: relative;
  height: 707px;
}

.contact__wrapper {
  background: white;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.571866) 3%, rgba(255, 255, 255, 0.969625) 67%);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.contact__inner {
  display: grid;
  grid-template-columns: 613px 500px;
  grid-template-rows: 1fr;
  gap: 0px 26px;
  grid-template-areas: ". .";
  position: relative;
  z-index: 1;
}

.contact__form {
  background-color: rgba(2, 22, 35, 0.67);
}
